import styled from "styled-components";

export default styled.button`
  display: block;
  position: relative;
  border: 1px solid #555;
  z-index: 5;
  font-size: 1.5rem;
  padding: 0.5rem;
  height: 3rem;
  background: #fff;
  font-family: inherit;
  border-radius: 3px;
  cursor: pointer;
`;
