export default {
  2: "<p>In U.S. national party primaries, public financing is available to candidates who meet certain guidelines. During a specified time period, the candidates collect donations from their supporters. After the time period ends, each candidate is eligible to receive a portion of public funds based on each candidate's total receipts.</p><p>The problem with this system is that it doesn't account for <em>how many</em> donors contributed -- only the total dollar amount. For example, if a million individual donors each contribute a dollar to one candidate, and a single donor contributes a million dollars to a different candidate, both candidates would receive the same amount of public funds. <p>In the U.S.A, this is alleviated by disallowing any donor from giving more than $2,700 per candidate.</p>",
  3: "<p>In the 2016 primary, Hillary Clinton received the largest total donations while Bernie Sanders received the most <em>individual</em> donations.</p><p>The yellow dots in the chart represent the portion of public funds made available to each candidate. As shown, the public funds don't change the competitive balance -- the candidate with the most donations always receives the most public funding.</p><p>Some argue this is a fair system. However, that requires accepting the notion that the more wealth you have, the more influence you should have in politics.</p>",
  4: ""
};
